<template>
    <div class="iframe">
        <top-bar :title="title"></top-bar>
        <div class="iframe-box">
          <iframe :src="url" scrolling="auto" frameborder="0"></iframe>
        </div>
        <fixedBar></fixedBar>
    </div>
</template>
<script>
import topBar from '@/components/common/TopBar'
import fixedBar from '@/components/common/FixedBar'
export default {
  components: {
    topBar,
    fixedBar
  },
  data () {
    return {
      url: '',
      title: ''
    }
  },
  created () {
    var reg = /浦发银行|交通银行|兴业银行/g
    if (reg.test(this.$route.query.name)) {
      location.href = this.$route.query.url
      return
    }
    this.url = this.$route.query.url
    if (this.$route.query.name) {
      this.title = this.$route.query.name
    }
  }
}
</script>
<style scoped>
.iframe{
    
    width: 375px;
    margin:0 auto;
    
}
.iframe-box{
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
    overflow-y: scroll;
     padding-top: 35px;
      padding-bottom: 48px;
      width: 100%;
     position: absolute;
    top:0;
    left:0;
    right:0;
    height: 100%;
    z-index: 1;
}
.iframe iframe{
    width:375px!important;
    height: 100%;
    margin:0 auto;
    border:none;
    overflow-x: hidden;
   
    background: url(../../assets/images/timg.gif) center center no-repeat;
    background-size: 100px 100px;

}
</style>



