<template>
    <div class="fixed-bar" :class="{bt50:isPositionClass}">
        <div class="left">
            <div class="logo">
                <img src="@/assets/images/pipi@2x.png" alt="">
            </div>
            <div class="title">
                <h2>轻松办卡领取500元现金</h2>
                <p>2步操作、3天下卡</p>
            </div>
        </div>
        <div class="download" @click="download">免费下载</div>
    </div>
</template>
<script>
    import openApp from '../../modules/openApp.js'

    export default {
      props: ['fixedPosition'],
      data () {
        return {
          isPositionClass: false
        }
      },
      methods: {
        download () {
          openApp.open()
        }
      },
      created () {
        if (this.fixedPosition) {
          this.isPositionClass = true
        }
      }
    }
</script>
<style scoped lang="scss">
.fixed-bar{
    position: fixed;
    height: 52px;
    background: rgba(0,0,0,0.70);
    bottom:0;
    width: 100%;
    display: flex;
    padding: 0 18px;
    align-items: center;
    justify-content: space-between;
    z-index: 9;
    .logo{
        width: 36px;
        height: 36px;
        margin-right: 10px;
        img{
            width: 100%;
            height: 100%;;
        }
    }
    .title{
        h2{
            font-size: 14px;
            color: #FFFFFF;
            letter-spacing: 0;
            line-height: 18px;
            font-weight: normal;
        }
        p{
            font-size: 10px;
            color: #FFFFFF;
            letter-spacing: 0;
        }
    }
    .left{
        display: flex;
    }
    .download{
        width: 90px;
        height: 30px;
        background: #4477DD;
        border-radius: 21px;
        color: #fff;
        text-align: center;
        line-height: 30px;
        font-size: 14px;
    }
}
.bt50{
    bottom: 54px;
}
</style>



