<template>
    <div class="top-bar"><div class="back" @click="bindBack()"></div>{{title||'财喵万卡'}}</div>
</template>
<script>
    export default {
      props: ['title'],
      data () {
        return {

        }
      },
      methods: {
        bindBack () {
          this.$router.back(-1)
        }
      }
    }
</script>
<style lang="scss" scoped>
.top-bar{
    height: 35px;
    position: fixed;
    top:0;
    width: 100%;
    text-align: center;
    line-height: 35px;
    background-color: #fff;
    font-size: 18px;
    z-index: 9;
   .back{
        height: 32px;
        width: 24px;
        background: url(../../assets/images/BACK@2x.png) no-repeat;
        background-size: cover;
        position: absolute;
        left:7px;
        top:50%;
        transform: translateY(-50%);

    }
}
</style>



